@use "sass:color";

$primary-color: #292559;
$secondary-color: color.adjust($primary-color, $lightness: 65%);

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    position: relative;
    font-family: system-ui;
    scroll-behavior: smooth;
}

body{
    padding: 0 20px;
}

h1, h2, h3, h4, h5, h6{
    color: $primary-color;
    text-align: center;
}

p{
    font-size: 18px;
}

nav{
    width: 100%;
    height: 100px;
    background-color: #f7f5ec;
    border: 2px solid $primary-color;
    margin-top: 20px;
    border-radius: 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 24px;

    img{
        width: 64px;

        &.atauni-logo{
            width: 80px;
        }
    }

    ul{
        list-style: none;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 60px;

        li{

            a{
                text-decoration: none;
                color: $primary-color;
                font-weight: bold;

                &:hover{
                    text-shadow: 0px 4px 4px $primary-color;
                }
            }
        }
    }
}

section {
    // width: 100vw;
    padding: 32px;
    gap: 24px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    margin-top: 20px;
    position: relative;

    h2{
        display: flex;
        justify-content: center;
        align-items: center;
        &::before {
            content: "";
            position: relative;
            display: inline-block;
            left: -20px;
            width: 60px;
            height: 3px;
            background-color: $primary-color;
        }

        &::after {
            content: "";
            position: relative;
            display: inline-block;
            right: -20px;
            width: 60px;
            height: 3px;
            background-color: $primary-color;
        }
    }

    &:nth-child(2n){
        background-color: $primary-color;
        h2{
            color: #f7f5ec;
            &::before {
                background-color: #f7f5ec;
            }
    
            &::after {
                background-color: #f7f5ec;
            }
        }
        p{
            color: #f7f5ec;
        }

        ul{
            display: flex;
            flex-direction: column;
            gap: 16px;
            align-self: flex-start;
            color: #f7f5ec;
            list-style: none;
            font-size: 18px;
            li{
                display: flex;
                justify-content: flex-start;
                align-items: center;
                gap: 8px;
                &:hover{
                    svg{
                        transform: translateX(4px);
                    }
                }
            }
        }
    }

    &#intro{
        p{
            line-height: 36px;
        }
        
    }

    &#presentation{
        width: 100%;
        padding: 32px 0;

        h2{
            display: flex;
            justify-content: center;
            align-items: center;

            .select-banner-or-program{
                display: inline-flex;
                border-radius: 12px;
                width: 360px;
                height: 60px;
                border: 2px solid $primary-color;
                button{
                    cursor: pointer;
                    height: 100%;
                    width: 180px;
                    border: none;
                    outline: none;
                    font-weight: bold;
                    font-size: 20px;
                    background-color: #f7f5ec;
                    color: $primary-color;
                    transition: background-color .5s, color .5s;

                    &:nth-child(1){
                        border-top-left-radius: 10px;
                        border-bottom-left-radius: 10px;
                    }

                    &:nth-child(2){
                        border-top-right-radius: 10px;
                        border-bottom-right-radius: 10px;
                    }

                    &.active{
                        background-color: $primary-color;
                        color: #f7f5ec;
                        text-shadow: 0 0 4px #f7f5ec;
                    }
                }
            }
        }
        

        // .present-buttons{
        //     width: 100%;
