        //         }
    
        //         &.active{
        //             svg{
        //                 color: $primary-color;
        //             }
        //             span{
        //                 position: absolute;
        //                 font-weight: bold;
        //                 color: #f7f5ec;
        //             }
        //         }
    
        //         &:hover{
        //             cursor: pointer;
                    
        //         }
    
        //         &:nth-child(1):hover{
        //             svg{
        //                 filter: brightness(1.5) drop-shadow(0px 8px 5px $primary-color);
        //             }
        //             span{
        //                 text-shadow: 0px 1px 5px white;
        //             }
