        //             }
        //             span{
        //                 text-shadow: 0px 1px 5px white;
        //             }
        //         }
        //     }
        // }
        

        table{
            width: 100%;
            tr{
                height: 48px;
                &.caption{
                    background-color: $primary-color;
                    color: #f7f5ec;
                }
                &.sub-caption{
                    background-color: $secondary-color;
                    color: $primary-color;
                }

                th, td{
                    padding: 12px 20px;
                    border: 3px solid $primary-color;
                    // text-align: center;
                }

                .long-column, .long-row{
                    text-align: center;
                    background-color: $secondary-color;
                }

                .long-row{
                    color: $primary-color;
                }

                td.lecture-name{
                    background-color: $primary-color;
                    color: #f7f5ec;
                }

                .long-column{
                    span{
                        display: inline-block;
                        margin-top: 8px;
                        font-weight: bold;
                        
                        &.lecture-no{
                            color: $primary-color;
                        }
                    }
                }
            }

            .lecture-name, .lecturer-name{
                font-weight: bold;
            }

            .lecturer-name{
                color: $primary-color;
            }

            ul{
                list-style: none;
                display: flex;
                flex-direction: column;
                gap: 8px;

                strong, svg{
                    color: $primary-color;
                }

                li{
                    display: flex;
                    justify-content: flex-start;
                    align-items: center;
                }

            }
        }

        img.banner{
            width: 100%;
        }
    }

    &#instructors {
        .cards {
            display: flex;
            gap: 32px;
            flex-wrap: wrap;
            align-items: center;
            justify-content: center;
            .instructor-card {
                padding: 30px 0 40px;
                margin-bottom: 30px;
                background-color: #f7f5ec;
                text-align: center;
                overflow: hidden;
                position: relative;
                width: 220px;
                border-radius: 12px;
    
                .picture {
                    display: inline-block;
                    height: 130px;
                    width: 130px;
                    margin-bottom: 50px;
                    z-index: 1;
                    position: relative;
    
                    &::before {
                        content: "";
                        width: 100%;
                        height: 0;
                        border-radius: 50%;
                        background-color: $primary-color;
                        position: absolute;
                        bottom: 135%;
                        right: 0;
                        left: 0;
                        opacity: 0.9;
                        transform: scale(3);
                        transition: all 0.3s linear 0s;
                    }
    
                    &::after {
                        content: "";
                        width: 100%;
                        height: 100%;
                        border-radius: 50%;
                        // background-color: $primary-color;
                        position: absolute;
                        top: 0;
                        left: 0;
                        z-index: -1;
                    }
    
                    img {
                        width: 100%;
                        height: auto;
                        border-radius: 50%;
                        transform: scale(1);
                        transition: all 0.9s ease 0s;
                    }
                }
    
                .title {
                    display: block;
                    font-size: 16px;
                    color: #4e5052;
                    text-transform: capitalize;
                }
    
                .about {
                    width: 100%;
                    height: 32px;
                    padding: 0;
                    margin: 0;
                    background-color: $primary-color;
                    position: absolute;
                    bottom: -100px;
                    left: 0;
                    transition: all 0.5s ease 0s;
                    a{
                        padding: 8px 0;
                        font-size: 12px;
                        color: white;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        width: 100%;
                        height: 100%;
                        transition: all 0.3s ease 0s;
                        text-decoration: none;
                    }
                }
    
                &:hover{
                    .picture{
                        img{
                            box-shadow: 0 0 1px 14px #f7f5ec;
                            border: 1px solid $primary-color;
                            transform: scale(0.7);
                        }
    
                        &::before{
                            height: 100%;
                        }
    
                        &::after{
                            background-color: $primary-color;
                        }
                    }
    
                    .about {
                        bottom: 0;
    
                        &:hover{
                            border-top-left-radius: 12px;
                            border-top-right-radius: 12px;
                            a{
                                color: #f7f5ec;
                                text-shadow: 0 0 5px white;
                            }
                        }
                    }
                }
            }
        }
    }
    
}

.fixed-up-button{
    width: 50px;
    height: 50px;
    position: fixed;
    bottom: 50px;
    right: 50px;
}